.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.6);
}

.modal {
    position: fixed;
    top: 0;
    right: 0;
    background-color: #fff;
    max-width: 100%;
    width: 400px;
    height: 100vh;
    z-index: 30;
    animation: slide-in 0.3s ease-in;
    overflow-y: auto;
}

@keyframes slide-in {
    0% { width: 0; }
    100% { width: 400px; }
}
